/* ABOUT PAGE */
.container{
    display: flex;
    flex-direction: row;
}
.about-image{
    width: 200px;
    height: 200px;
    display: flex;
    align-items: flex-start;
    padding-right: 80px;
}
.about-container{
    display: flex;
    font-family: circularstd;
    flex-direction: column;
}

.about-header{
    font-family: circularstd;
    font-size: 25px;
    text-align: left;
}

.about-me{
    padding-top: 20px;
    font-size: 17.5px !important;
    font-family: circularstd;
    font-style: normal;
}

.about-skills-header{
    font-family: circularstd;
    font-size: 25px;
    text-align: left;
    padding-top: 20px;
}
.skills-languages{
    font-size: 17.5px !important;
    font-family: circularstd;
    line-height: 30px;
    justify-content: space-evenly;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
ul{
    list-style: none;
}

/* PROJECTS PAGE */
.projects-container{
    font-family: circularstd;
    display: flex;
    flex-direction: column;
}

.projects-header{
    margin-top: 10px;
    font-size: 25px;
}

.projects-subheader{
    font-size: 20px;
}

/* Project 1*/
.projects-1-container{
    display: flex;
    flex-direction: row;
    width: 100%;
}
.projects-description{
    font-size: 20px;
    position: relative;
    flex-direction: end;
    text-align: right;
}
.projects-1{
    margin-top: 12.5px;
    position: relative;
    width: 50%;
    height: 200px;
}

.overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #DBCFC7;
}

/*#FFDBCFC7*/

.projects-1:hover .overlay{
    opacity: .9;
}

.projects-1-image{
    position: absolute;
    width: 100%;
    height: 100%;
}

.overlay-text{
    position: relative;
    color: white;
    height: 350px;
    font-size: 25px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 1170px) {
    .container{
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    .about-image{
        width: 200px;
        height: 200px;
        display: flex;
        align-items: flex-start;
        padding-right: 0px;
        padding-bottom: 50px;
    }
    .projects-1{
        height: 200px;
    }

    .projects-1-container{
        display: flex;
        flex-direction: row;
    }
